.categories {
  &__wrapper {
    display: grid;
    row-gap: 40px;

    @include onTablet {
      row-gap: 56px;
    }
  }
}

.category {
  &__photos {
    @include pageGrid;

    margin-bottom: 16px;
  }
  &__link {
    position: relative;
    &--wide {
      @include onTablet {
        grid-column: span 4;
      }

      @include onDesktop {
        grid-column: span 8;
      }
    }

    &--square {
      height: 0;
      padding-bottom: 100%;

      @include onTablet {
        grid-column: span 2;
      }

      @include onDesktop {
        grid-column: span 4;
      }
    }
  }
  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;

    @include hover(transform, scale(1.1));
  }

  &__title {
    margin: 0;
    font-family: $secondary-font-family;
    font-size: 24px;
    font-weight: 700;
    line-height: 33.6px;
    text-align: left;
    cursor: pointer;
    transition: all 0.3s ease;
  }

  &__title:hover {
    color: rgb(180, 163, 12);
  }
}
